import React, { Component } from 'react'
import { Location } from '@reach/router'
import styles from './styles.module.scss'
import { Link } from 'gatsby'
import GitHubLogo from '@fortawesome/fontawesome-free/svgs/brands/github.svg'
import LinkedInLogo from '@fortawesome/fontawesome-free/svgs/brands/linkedin.svg'
import InstagramLogo from '@fortawesome/fontawesome-free/svgs/brands/instagram.svg'
import MenuLogo from '@fortawesome/fontawesome-free/svgs/solid/bars.svg'
import TimesIcon from '@fortawesome/fontawesome-free/svgs/solid/times-circle.svg'
import {
  withGitHubLink,
  withLinkedInLink,
  withInstagramLink
} from '../withSocialLink'
import joinClasses from '../../utils/joinClasses'

const NavLink = ({ children, to, onClick }) => {
  return (
    <Location>
      {({ location }) => {
        const classNames = joinClasses([
          'nav-link nav-item',
          location.pathname !== to ? '' : 'active',
          styles.navLink
        ])

        return (
          <Link className={classNames} to={to} onClick={onClick}>
            {children}
          </Link>
        )
      }}
    </Location>
  )
}

/* eslint-disable */
const HeaderExternalLink = ({ className, ...props }) => {
  return (
    <a
      {...props}
      className={joinClasses([
        className,
        styles.socialLogo,
        'd-none d-sm-inline',
      ])}
    />
  )
}
/* eslint-enable */

const GitHubLink = withGitHubLink(HeaderExternalLink)
const LinkedInLink = withLinkedInLink(HeaderExternalLink)
const InstagramLink = withInstagramLink(HeaderExternalLink)

function stickyNav ({ current: header }, { current: navWrapper }) {
  return () => {
    // When scroll overpass nav position
    if (window.pageYOffset >= navWrapper.offsetTop) {
      // Make content sticky -> Fixed to the body page
      navWrapper.classList.add('nav-wrapper--sticky')
    }
    // When scroll is over the nav
    if (header.offsetHeight > window.pageYOffset) {
      // Clean nav classes, leave it in its initial position
      navWrapper.classList.remove('nav-wrapper--sticky')
    }
  }
}

class Header extends Component {
  constructor (props) {
    super(props)

    this.state = {
      menuOpen: false
    }

    this.toggleMenu = this.toggleMenu.bind(this)
    this.closeMenu = this.closeMenu.bind(this)

    this.headerRef = React.createRef()
    this.navWrapperRef = React.createRef()
  }

  componentDidMount () {
    if (window.matchMedia('(min-width: 576px)').matches) {
      window.onscroll = stickyNav(this.headerRef, this.navWrapperRef)
    }
  }

  toggleMenu () {
    this.setState({ menuOpen: !this.state.menuOpen })
  }

  closeMenu () {
    this.setState({ menuOpen: false })
  }

  render () {
    const showClass = this.state.menuOpen ? 'show' : ''

    return (
      <header
        className={joinClasses([styles.header, 'container pt-1 px-md-2 pb-1'])}
        ref={this.headerRef}
      >
        <GitHubLink className={styles.gitHubLogo}>
          <GitHubLogo />
        </GitHubLink>
        <LinkedInLink className={styles.linkedInLogo}>
          <LinkedInLogo />
        </LinkedInLink>
        <InstagramLink className={styles.instagramLogo}>
          <InstagramLogo />
        </InstagramLink>

        <button
          className={joinClasses([styles.menu, 'btn-link btn'])}
          type='button'
          onClick={this.toggleMenu}
        >
          <MenuLogo />
        </button>

        <h1
          className={joinClasses([
            styles.headerTitle,
            'text-right text-sm-center mb-1'
          ])}
        >
          Luis Carlos Ruiz Delgado
        </h1>
        <p
          className={joinClasses([
            styles.headerInfo,
            'text-right text-sm-center mb-0 mb-sm-2'
          ])}
        >
          <span className='d-none d-sm-inline'>
            Location: Guatemala City | Phone: +50235249629 | Email:{' '}
          </span>
          ing.lkruiz@gmail.com
        </p>

        <div className={styles.navWrapper} ref={this.navWrapperRef}>
          <nav
            className={joinClasses(['nav nav-pills', styles.nav, showClass])}
          >
            <button
              className={joinClasses([styles.close, 'btn btn-link'])}
              onClick={this.toggleMenu}
            >
              <TimesIcon />
            </button>
            {[
              {
                to: '/',
                children: 'Home'
              },
              {
                to: '#',
                children: 'Blog'
              },
              {
                to: '/resume/',
                children: 'Resume'
              }
            ].map(linkProps => (
              <NavLink
                key={linkProps.to}
                {...linkProps}
                onClick={this.closeMenu}
              />
            ))}
          </nav>
        </div>

        <div className={joinClasses([styles.navBackdrop, showClass])} />
      </header>
    )
  }
}

export default Header
