import React from 'react'

const withNoReferrerNoOpener = WrappedComponent => {
  return props => (
    <WrappedComponent
      {...props}
      target='_blank'
      rel='external author noreferrer noopener'
    />
  )
}

export function withGitHubLink (WrappedComponent) {
  return withNoReferrerNoOpener(props => (
    <WrappedComponent {...props} href='https://github.com/inglkruiz/' />
  ))
}

export function withLinkedInLink (WrappedComponent) {
  return withNoReferrerNoOpener(props => (
    <WrappedComponent
      {...props}
      href='https://www.linkedin.com/in/inglkruiz/'
    />
  ))
}

export function withInstagramLink (WrappedComponent) {
  return withNoReferrerNoOpener(props => (
    <WrappedComponent {...props} href='https://www.instagram.com/inglkruiz/' />
  ))
}
